/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: 900;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
  height: 100%;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.center-content {
  max-width: 80rem;
}

.align-rigth {
  width: 100%;
  display: flex;
  justify-content: right;
}

/** Media Queries **/
.button-yellow {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.button-yellow:hover {
  cursor: pointer;
  background-color: #c77808;
}

.button-yellow-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.button-yellow-block:hover {
  cursor: pointer;
  background-color: #c77808;
}

.button-green {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.button-green:hover {
  cursor: pointer;
  background-color: #619800;
}

/* UI */
/** Media Queries **/
.no-webp .start {
  background-image: url(../../img/header.jpg);
}

.webp .start {
  background-image: url(../../../build/img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.start {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.start {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.start .container-header {
    min-height: 70rem;
  }
}

.container-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.container-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  background-color: #333333;
  padding: 2.5rem 0;
  margin-bottom: 0;
}
.footer .container-footer {
  padding-top: 2.5rem;
}
.footer .copyrigth {
  color: #FFFFFF;
  text-align: center;
}

/** Media Queries **/
.bar {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navigation {
  display: none;
}
@media (min-width: 768px) {
  .navigation {
    display: flex;
    align-items: center;
  }
}
.navigation a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navigation a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navigation a:last-of-type {
    margin: 0;
  }
}

/* Menu for mobile screen */
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

/** Media Queries **/
.icon {
  text-align: center;
}
.icon img {
  height: 10rem;
  width: auto;
  margin: 0 auto;
}
.icon h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .icons-us {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
/** Media Queries **/
@media (min-width: 768px) {
  .container-adverts {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.advert {
  background-color: #eeeeee;
  border: 1px solid #d4d4d4;
}
.advert .content-advert {
  padding: 2rem;
}
.advert p,
.advert h3 {
  margin: 0;
}

.price {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.icons-features {
  display: flex;
  list-style: none;
  padding: 0;
}
.icons-features li {
  height: 3rem;
  margin-left: 3rem;
  flex: 1;
  display: flex;
}
.icons-features li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.icons-features li p {
  font-weight: 700;
}

/** Media Queries **/
.no-webp .image-contact {
  background-image: url(../../img/encuentra.jpg);
}

.webp .image-contact {
  background-image: url(../../../build/img/encuentra.webp);
}

.image-contact {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.image-contact h2,
.image-contact p {
  color: #FFFFFF;
}
.image-contact h2 {
  font-size: 4rem;
  font-weight: 900;
}
.image-contact p {
  font-size: 1.8rem;
}

/** Media Queries **/
@media (min-width: 768px) {
  .lower-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entry-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entry-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entry-blog:last-of-type {
  margin-bottom: 0;
}

.meta-information span {
  color: #E08709;
}

.text-entry a {
  color: black;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .text-entry a {
    margin-top: 0;
  }
}
.text-entry h4 {
  margin: 0;
  line-height: 1.4;
}
.text-entry h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}

/** Media Queries **/
.comment {
  background-color: #71B100;
  font-size: 1.8rem;
  padding: 1rem;
  color: #FFFFFF;
  border-radius: 3rem;
}
.comment blockquote {
  padding-left: 5rem;
  position: relative;
}
.comment blockquote::before {
  position: absolute;
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  left: -2rem;
}
.comment p {
  text-align: right;
  margin-right: 2rem;
}

.form p {
  font-size: 1.4rem;
  color: #333333;
  margin-top: 2rem;
}
.form legend {
  font-size: 2rem;
  color: #333333;
}
.form label {
  font-weight: 700;
  display: block;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 80%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1.2rem;
  margin-bottom: 2rem;
}
.form textarea {
  height: 20rem;
}
.form .form-contact {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form input[type=radio] {
  margin: 0;
  width: auto;
}

/* Internal Pages */
/** Media Queries **/
.content-us {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}

.text-us blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 0;
  padding-left: 1rem 0 3rem 0;
}